@import '~node_modules/modern-normalize/modern-normalize.css';
:root {
    --text-color-primary: #333333;
    --text-color-secondary: #ffffff;
    --color-brand: #28a745;
    --background-color-primary: #ffffff;
    --background-color-blog: #0284d0;
    --background-color-footer: #333333;
    --background-color-header: rgba(0, 0, 0, 0.8);
    --background-gradient-wrapper: rgba(0, 0, 0, 0.4);
    --color-text-hero: #e9e9e9;
    --color-text-other: #808080;
}

$timing-function: cubic-bezier(0.4, 0, 0.2, 1);

body {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    color: var(--text-color-primary);
    background-color: var(--background-color-primary);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin: 0;
    padding: 0;
}

img {
    max-width: 100%;
    display: block;
    height: auto;
}

// .list {
//     list-style: none;
// }

.link {
    display: block;
    text-decoration: none;
    color: inherit;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.container {
    width: 1360px;
    padding: 0 28px;
    margin-left: auto;
    margin-right: auto;
}

.wrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(
        0deg,
        var(--background-gradient-wrapper),
        var(--background-gradient-wrapper)
      ),
      url('../images/hero-overlay.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) { 
            background-image: linear-gradient(
                0deg,
                var(--background-gradient-wrapper),
                var(--background-gradient-wrapper)
              ),
              url(../images/hero-overlay2x.jpg);
    }

    @media screen and (min-device-pixel-ratio: 3),
    screen and (min-resolution: 288dpi),
    screen and (min-resolution: 3dppx) {
            background-image: linear-gradient(
                0deg,
                var(--background-gradient-wrapper),
                var(--background-gradient-wrapper)
              ),
              url(../images/hero-overlay3x.jpg);
    }
}